body{
	background: fixed no-repeat url("./apt1.jpg");
	background-size: cover;
	background-repeat: no-repeat;
}
.card-photo{
	display: -ms-flexbox;
	display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    overflow: hidden
}
.card-photo img{
	-ms-flex-negative: 0;
	    flex-shrink: 0;
    min-width: 100%;
    min-height: 100%
}
.card{
    margin: 0 0 10px 0;
}

.fixed-top-custom{
	width: 100%;
	height: 70px;
	opacity: 0.9;
	background-color: #ebebeb;
}
.nav-buttons{
	text-align: right;
}
h1{
	color: #007bff;
}
.proManager{
	text-align: center;
	width: 110px;
	height: 120px;
	background-color: #3dce5c;
	border-radius: 0 0 10% 10%;
	color: white;
	-webkit-text-decoration-line: none;
	        text-decoration-line: none;
}
.logo:hover{
	-webkit-text-decoration-line: none;
	        text-decoration-line: none;
}
.proManager:hover{
	color: #3dce5c;
	background-color: #ebebeb;
	-webkit-text-decoration-line: none;
	        text-decoration-line: none;
	border: 1px solid #3dce5c;
}

#logoff{
	color: black;
}

#logoff:hover{
	-webkit-text-decoration-line: none;
	        text-decoration-line: none;
}

.btn-success{
	background-color: #3dce5c;
	border: 1px solid #3dce5c;	
}

.btn-success:hover{
	color: #3dce5c;	
	border: 1px solid #3dce5c;	
	background-color: white;	
}

.panelBox {
    height: 200px;
    width: inherit;
    background-color: black; 
	opacity: .5;
    position: absolute;
}
.searchBox{
	opacity: .9;
	padding: 20px 10px;
	position: relative;                  
}
.btn-outline-secondary{
	border: none;
}
.btn-outline-secondary:hover{
	background-color: transparent !important;
}
span.icon img{
	heigth: 20px;
	width: 20px;
}
